// SECTION scheduler :: START

.scheduler {
  &__title {
    padding-bottom: 40px;
    font-weight: 900;
    font-size: 40px;
    line-height: 50px;
    color: #000;
    @include adaptive(1279) {
      font-size: 34px;
      line-height: 40px;
    }
    @include adaptive(767) {
      padding-bottom: 30px;
      font-size: 24px;
      line-height: normal;
    }

    &--white {
      color: #ffffff;
    }
  }

  &__header {
    overflow: hidden;
    position: relative;
    background-color: #ffffff;

    .c-grid {
      position: relative;
    }

    &-wrapper {
      display: flex;
      align-items: center;
      height: 158px;

      &-left {
        display: flex;
        align-items: center;

        span {
          display: block;
          width: 2px;
          height: 38px;
          margin-left: 20px;
          margin-right: 20px;
          background-color: rgba(#00449D, 0.1);
        }

        p {
          font-weight: 300;
          font-size: 20px;
          color: #000;
        }
      }
    }

    &-marker {
      position: absolute;
      top: -6px;
      right: 0;
    }

    svg {
      fill: #00449D;
    }
  }

  &__main {
    padding-top: 110px;
    padding-bottom: 120px;
    background-color: #f8fafc;

    &-subtitle {
      padding-bottom: 24px;
      font-weight: 300;
      font-size: 26px;
      color: #000;
    }

    &-title {
      padding-bottom: 37px;
      font-weight: normal;
      font-size: 50px;
      line-height: 70px;
      color: #000;
    }

    &-heading {
      padding-bottom: 40px;
      font-weight: 900;
      font-size: 60px;
      line-height: 70px;
      color: #000;
    }

    &-dropdown {
      position: relative;
      display: flex;
      align-items: center;

      &-wrapper {
        display: flex;
        align-items: center;
        padding-bottom: 50px;

        > div {
          padding-right: 60px;
        }
      }

      &-title {
        padding-right: 16px;
        font-size: 20px;
        color: #222;
      }

      &-btn {
        cursor: pointer;
        padding-right: 7px;
        font-size: 20px;
        color: #00449D;

        svg {
          width: 10px;
          height: 10px;
          transform: rotate(90deg);
          fill: #00449D;
        }
      }

      &-menu {
        z-index: 899;
        display: none;
        position: absolute;
        top: calc(100% + 5px);
        right: 0;
        padding-top: 2px;
        padding-bottom: 3px;
        border-radius: 10px;
        background-color: #fff;
        box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      }

      &-link {
        display: flex;
        justify-content: flex-end;
        padding: 5px 15px 5px 30px;
        color: #00449D;
        font-size: 16px;
        line-height: 35px;
        @include transition(background-color);

        &:hover {
          background-color: lighten(#00449D, 67%);
          @include transition(background-color);
        }
      }
    }

    &-box {
      cursor: pointer;
      height: 100%;
      padding: 30px 15px 30px 30px;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      @include transition(background-color);

      &:hover {
        background-color: #00449d;
        @include transition(background-color);

        p {
          color: #ffffff;
          @include transition(color);
        }
      }

      &-wrapper {
        display: flex;
        flex-wrap: wrap;
        width: calc(100% + 40px);
        margin-left: -40px;

        > div {
          width: 100%;
          max-width: calc(100% / 3);
          padding-left: 40px;
          padding-bottom: 40px;
        }
      }

      p {
        @include transition(color);
      }

      &-title {
        padding-bottom: 5px;
        font-weight: 900;
        font-size: 22px;
        color: #000;
      }

      &-subtitle {
        padding-bottom: 15px;
        font-size: 18px;
        color: #222;
      }

      &-desc {
        overflow: hidden;
        position: relative;
        height: 44px;
        font-size: 16px;
        line-height: 22px;
        color: rgba(#222, 0.4);

        //textarea {
        //  z-index: -1;
        //  opacity: 0;
        //  visibility: hidden;
        //  resize: none;
        //  position: absolute;
        //  top: 0;
        //  left: 0;
        //  width: 100%;
        //  height: auto;
        //  padding: 0;
        //  margin: 0;
        //  border: none;
        //  border-radius: 0;
        //  box-shadow: none;
        //  background-color: transparent;
        //  line-height: 22px;
        //}
      }
    }

    &-btn {
      display: none;
      
      .c-btn {
        max-width: unset;
        width: auto;
        padding-left: 60px;
        padding-right: 60px;

        &.is-active {
          svg {
            transform: rotate(-90deg);
            @include transition(transform);
          }
        }

        p {
          padding-right: 5px;
        }

        span {
          padding-right: 20px;
        }
      }

      svg {
        position: relative;
        top: 2px;
        width: 12px;
        height: 12px;
        transform: rotate(90deg);
        backface-visibility: hidden;
        transform-origin: center;
        @include transition(transform);
      }
    }
  }

  &__data {
    //display: none;
    padding-top: 129px;
    padding-bottom: 60px;

    &-list {
      display: flex;
      flex-direction: column;
      padding-bottom: 115px;

      li {
        display: flex;
        align-items: center;
        padding-bottom: 20px;

        &:last-of-type {
          padding-bottom: 0;
        }

        > div {
          &:nth-of-type(1) {
            width: 40px;
            padding-top: 3px;
          }
          &:nth-of-type(2) {
            width: 100%;
            max-width: 180px;
          }
          &:nth-of-type(3) {
            flex: 1;
            min-width: 1px;
            max-width: 100%;
          }
        }

        p,
        span {
          font-size: 20px;
          color: #000;
        }

        p {
          font-weight: 700;
        }
      }
    }

    &-option {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-bottom: 35px;

      > div {
        &:nth-of-type(2) {
          padding-left: 15px;
          padding-right: 15px;
        }
      }

      p {
        font-size: 26px;
        text-align: center;
        color: #000;
      }

      a {
        position: relative;
        display: flex;
        align-items: center;
        @include transition(color);

        &.swiper-button-disabled {
          pointer-events: none;
          color: rgba(#000, 0.4);
          @include transition(color);

          span {
            color: rgba(#000, 0.4);
            @include transition(color);
          }
        }
      }

      svg {
        width: 21px;
        height: 21px;
      }

      span {
        font-weight: bold;
        font-size: 20px;
        color: #00449d;
        @include transition(color);
      }
    }

    &-prev {
      left: -15px;

      svg {
        margin-right: 20px;
      }
    }

    &-next {
      right: -15px;

      svg {
        margin-left: 20px;
        transform: scale(-1);
      }
    }

    &-slider {
      overflow: hidden;
      position: relative;
      left: -20px;
      width: calc(100% + 40px);
      padding-left: 20px;
      padding-right: 20px;
      padding-bottom: 30px;
    }

    &-slide {
      position: relative;
      display: flex;
      align-items: center;
      flex-direction: column;
      width: 100%;
      height: 145px;
      padding: 10px;
      border-radius: 10px;
      background: #fff;
      box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.05);
      @include transition(box-shadow, background-color);

      &.is-active,
      &:hover {
        box-shadow: none;
        background-color: #00449d;
        @include transition(box-shadow, background-color);

        span,
        p,
        h4 {
          color: #ffffff;
          @include transition(color);
        }

        i {

        }
      }

      &.scheduler__data-slide--disabled {
        pointer-events: none;
        box-shadow: none;
        background-color: #f8f8f8;
        @include transition(box-shadow, background-color);

        span,
        p,
        h4 {
          color: rgba(#000, 0.4);
          @include transition(color);
        }
      }

      &.scheduler__data-slide--today {
        i,
        span {
          opacity: 1;
          visibility: visible;
        }
      }

      &--top {
        width: 100%;
        padding: 0 7px;
      }

      i {
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 10px;
        right: 10px;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #cd084b;
      }

      span,
      p,
      h4 {
        @include transition(color);
      }

      span {
        opacity: 0;
        visibility: hidden;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 10px;
        letter-spacing: 0.2em;
        line-height: 20px;
        text-align: left;
        color: #cd084b;
      }

      p {
        padding-bottom: 20px;
        font-weight: 600;
        font-size: 16px;
        line-height: 20px;
        color: #000;
      }

      h4 {
        font-weight: 600;
        font-size: 50px;
        line-height: 40px;
        color: #000;
      }
    }
  }

  &__time {
    //display: none;
    padding-top: 120px;
    padding-bottom: 140px;
    background-color: #f8fafc;

    &-row {
      display: flex;
      justify-content: space-between;

      &-1 {
        padding-bottom: 60px;
      }
    }

    &-col {
      width: 100%;
      max-width: calc((100% - 40px) / 2);
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 60px;
      margin-bottom: 20px;
      border-radius: 10px;
      background-color: #fff;
      box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.05);
      @include transition(box-shadow, background-color);

      &.is-active,
      &:hover {
        box-shadow: none;
        background-color: #00449d;
        @include transition(box-shadow, background-color);

        span {
          color: #ffffff;
          @include transition(color);
        }
      }

      span {
        font-size: 20px;
        line-height: 30px;
        text-align: center;
        color: #000;
        @include transition(color);
      }
    }

    .c-form {
      &__label {
        color: rgba(#000, 0.5);
      }

      &__select {
        border: 2px solid rgba(0, 0, 0, 0.05);
      }
    }
  }

  &__information {
    //display: none;

    &-copyright {
      font-size: 20px;
      line-height: 30px;
      color: #5390e3;
      @include adaptive(767) {
        font-size: 16px;
        line-height: normal;
      }
    }

    &.request {
      &:before {
        top: auto;
        right: -160px;
        bottom: -176px;
        margin-right: 0;
        background-size: contain;
        @include adaptive(767) {
          right: -75px;
          bottom: -75px;
          background-position: bottom right;
        }
      }

      .request {
        &__bg {
          width: 100%;
          margin-left: 0;
          border-radius: 0;
        }

        &__wrapper {
          padding-top: 120px;
          padding-bottom: 120px;
          @include adaptive(767) {
            padding-top: 60px;
            padding-bottom: 60px;
          }
        }
      }

      .c-form {
        &__row {
          padding-left: 0;
          padding-right: 0;
        }

        &__textarea {
          resize: none;
          border-radius: 10px;
          background-color: #fff;
          padding: 10px 20px;
          border: none;
          box-shadow: none;
          color: #000;
          font-size: 18px;

          &-wrapper {
            width: 100%;
            height: 200px;
          }
        }

        &__field {
          &--textarea {
            width: 100%;
          }

          &--btn {
            flex-direction: row;
            justify-content: flex-start;
            padding-bottom: 139px;
            @include adaptive(767) {
              padding-bottom: 100px;
            }

            .c-btn {
              max-width: 213px;
              margin-left: 0;
              margin-right: 20px;

              &:last-of-type {
                margin-right: 0;
              }

              &.c-btn--border {
                border: 2px solid #5390e3;
                color: #ffffff;
                @include transition(border, background-color, color);

                &:hover {
                  border-color: #CD084B;
                  @include transition(border, background-color, color);
                }
              }
            }
          }
        }
      }
    }
  }
}

// SECTION scheduler :: END